import ProgressBar from 'react-bootstrap/ProgressBar'
import Button from 'react-bootstrap/Button'
import Form from 'react-bootstrap/Form'
import Nav from 'react-bootstrap/Nav'
import FormControl from 'react-bootstrap/FormControl'

import AlertsList from './components/alerts-list.component'
import TeamBio from './components/team-bio.component'
import TradingInfo from './components/trading-info.component'
import NewAlert from "./components/new-alert.component";

const stripePromise = loadStripe('pk_test_51IDpHFIRiN8NewytzC0856E85t72Bd2zF2Kn7sWSRgrZS8HlcQmCQNdEbXDqLrFIXuPJIf5arF9ojL7V5l32hpvL00UaiQd0BI')
let memberCount = 777;
// let userKey = 'PASSWORD';

function signUpText(members) {
  let dollars=0;
  let tier=0;
  let prevTier=0;
  if (members<100) {dollars=50; tier=100; prevTier=0;}
  else if (members<500) {dollars=100; tier=400; prevTier=100;}
  else if (members<1500) {dollars=150; tier=1000; prevTier=500;}
  else if (members<3000) {dollars=200; tier=1500; prevTier=1500}
  else return 'All slots filled! Grab some popcorn while you watch us win :)'

  return tier + prevTier - members + ' slots open for $' + dollars + '/mo pricing';
}

function App() {
  const [count, setCount] = useState(memberCount);
  // const [key, setKey] = useState(userKey);
  const [isLoading, setLoading] = useState(false);

  const checkoutClick = async (event) => {
    setLoading(true);
    // Get Stripe.js instance
    const stripe = await stripePromise;
    // Call your backend to create the Checkout Session
    const response = await axios.post('https://alertigo-api.herokuapp.com/create-checkout-session');
    // console.log(response);
    const session = await response.data;

    // When the customer clicks on the button, redirect them to Checkout.
    const result = await stripe.redirectToCheckout({
      sessionId: session.id,
    });
    if (result.error) {
      // If `redirectToCheckout` fails due to a browser or network
      // error, display the localized error message to your customer
      // using `result.error.message`.
      console.log(result.error.message);
    }
  }

  useEffect(() => {
    if (isLoading) {
      checkoutClick().then(() => {
        setLoading(false);
      });
    }
  }, [isLoading]);


  return (
    // <Container fluid className='p-0'>
    <Router>
      <Navbar bg='secondary' expand='xl' sticky='top' className='shadow-lg text-left justify-content-left mb-4'>
        <Navbar.Brand className='ml-xl-auto'>
          <Jumbotron className='text-light bg-secondary p-2 mb-0'>
            <h1 className='mb-0'>ALERTIGO</h1>
            <p className='d-none d-xl-block mb-0'>LEARN TO TRADE 1% BETTER THAN THE COMPETITION</p>
          </Jumbotron>
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="main-nav"/>
        <Navbar.Collapse id="main-nav">
          <Nav className='ml-auto'>
            <Nav.Link className='text-light' as={Link} to='/'>HOME</Nav.Link>
            <Nav.Link className='text-light' as={Link} to='/alerts'>ALERTS</Nav.Link>
            <Nav.Link className='text-light' as={Link} to='/add'>NEW ALERT</Nav.Link>
            <Nav.Link className='text-light'>Use textfield to simulate members</Nav.Link>
          </Nav>
          {/* COUNTER
          <Navbar.Text>
            <input className='text-dark ml-3' style={{width: '2.5em', background: 'none', border: 'none'}} type='text' value={count} onChange={(e) => setCount(e.target.value)} />
            <span className='text-dark'>MEMBERS & COUNTING</span>
          </Navbar.Text> */}
          <Form inline>
            <FormControl type="text" placeholder="Search" className="w-25 m-3" value={count} onChange={(e) => setCount(e.target.value)} />
            {/* <FormControl type="text" placeholder="Search" className="m-3" value={key} onChange={(e) => setKey(e.target.value)} /> */}
            {/* <Button variant="outline-dark">SIGN IN</Button> */}
          </Form>
        </Navbar.Collapse>
      </Navbar>
      {/* <Container> */}
        {/* NAVBAR */}
        {/* ALERTS */}
        {/* <AlertsList/>  */}
        {/* WHO TF ARE YOU */}
        {/* <TeamBio/> */}
        {/* WHAT IS TRADING */}
        {/* <TradingInfo/> */}
        {/* SIGN UP SECTION */}
      {/* </Container> */}
      <Route path="/" exact>
        <TeamBio/>
        <TradingInfo/>
      </Route>
      <Route path="/alerts" component={AlertsList} />
      <Route path="/add" component={NewAlert} />
      <Navbar bg='secondary' className='shadow-lg mt-5'>
        <Container className='justify-content-center'>
          <Row className='justify-content-center align-items-center p-5'>
            <Col xs={12} md={6} lg={8} className='text-center mb-4'>
              <Button size='lg' variant='primary' className='shadow text-dark p-4' disabled={isLoading || count>=3000} onClick={!isLoading ? checkoutClick : null}>
                <h1 className='font-weight-light'>{count>=3000 ? "CAN'T" : ''} {isLoading ? 'LOADING...' : 'SUBSCRIBE TO ALERTIGO'}</h1>
                <h4>{isLoading ? "Welcome to financial freedom" : signUpText(count)}</h4>
              </Button>
            </Col>
            <Col xs={12} md={6} lg={4}>
              <h3 className={(count>0 ? 'w-100 text-light' : 'd-none')}>$50/MONTH:<br/>100 MEMBERS</h3>
              <ProgressBar className={(count>0 ? 'w-100 mb-2 shadow-lg' : 'd-none')} min={0} max={100} now={count} label={((count/100)*100) > 25 ? ((count>100 ? 100 : count) + '/100') : ''} />
              <h3 className={(count-100>0 ? 'w-100 text-light' : 'd-none')}>$100/MONTH:<br/>400 MEMBERS</h3>
              <ProgressBar className={(count-100>0 ? 'w-100 mb-2 shadow-lg' : 'd-none')} min={100} max={500} now={count} label={(((count-100)/400)*100) > 25 ? ((count-100>400 ? 400 : count-100) + '/400') : ''}/>
              <h3 className={(count-500>0 ? 'w-100 text-light' : 'd-none')}>$150/MONTH:<br/>1000 MEMBERS</h3>
              <ProgressBar className={(count-500>0 ? 'w-100 mb-2 shadow-lg' : 'd-none')} min={500} max={1500} now={count} label={(((count-500)/1000)*100) > 25 ? ((count-500>1000 ? 1000 : count-500) + '/1000') : ''}/>
              <h3 className={(count-1500>0 ? 'w-100 text-light' : 'd-none')}>$200/MONTH:<br/>1500 MEMBERS</h3>
            </Col>
          </Row>
        </Container>
      </Navbar>
    </Router>
    // {/* </Container> */}
  );
}

export default App;
