import "bootstrap/dist/css/bootstrap.min.css";
import React, {Component} from 'react';
import '../scss/custom.scss'

import axios from 'axios';

//react bootstrap imports
import Accordion from 'react-bootstrap/Accordion'
import Card from 'react-bootstrap/Card'
import Button from 'react-bootstrap/Button'
// import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Container from 'react-bootstrap/Container'

const formatDate = (time) => {
    // console.log(time)
    let [year, month, date] = time.split("-")
    let newDate = date.substring(0, 2)
    let newYear = year.substring(2, 4)
    return month+'/'+newDate+'/'+newYear
}

const formatTime = (time) => {
    let offset = (new Date().getTimezoneOffset())/60
    let [date, timeStr] = time.split("T")
    let hours = timeStr.substring(0,2) - offset
    let mins = timeStr.substring(3,5)
    let ampm = ''
    if(hours>12) {
        hours-=12
        ampm="PM"
    } else if (hours===0) {
        hours=12
        ampm="AM"
    } else {
        ampm="AM"
    }
    return hours + ':' + mins + " " + ampm
}

const Alert = props => (
    <Col xs={12} md={6} lg={4} xl={3}>
        <Card className=' bg-primary border-0 mt-4 p-4'>
            {/* <Card.Header className='text-center border-0 bg-transparent'> */}
            <Accordion.Toggle as={Button} variant='primary' eventKey={props.num}>
                <h1 className='text-dark'>{props.alert.ticker}</h1>
                <h4>{formatDate(props.alert.time)}</h4>
                <p className='mb-0'>{formatTime(props.alert.time)}</p>
            </Accordion.Toggle>
            {/* </Card.Header> */}
            <Accordion.Collapse eventKey={props.num}>
                <Card.Body className='p-0 mt-3 text-center'> 
                    <h2>{props.alert.stance ? 'BULLISH' : 'BEARISH'}</h2>
                    <h6>ENTRY PRICE: {props.alert.entry}</h6>
                    <h6>EXIT PRICE: {props.alert.exit}</h6>
                    <p className='mb-0'>{props.alert.synopsis}</p>
                    <h4>Posted by: {props.alert.author}</h4>
                </Card.Body>
            </Accordion.Collapse>
        </Card>
    </Col>
)

export default class AlertsList extends Component {
    constructor(props){
        super(props);
        this.state={alerts: []};
    }

    componentDidMount() {
        axios.get('https://alertigo-api.herokuapp.com/alerts')
            .then(response => {
                this.setState({alerts: response.data});
            })
            .catch((error) => {
                console.log(error);
            })
    }

    alertList() {
        let val=0    
        return this.state.alerts.reverse().map((currentAlert, i) => {
            val++;
            return <Alert alert={currentAlert} num={val} key={i}></Alert>
        })
    }

    render() {
        return (
            <Container className='mt-2'>
                <Accordion className='row'>
                    {this.alertList()}
                </Accordion>
            </Container>
        )
    }
}