import "bootstrap/dist/css/bootstrap.min.css";
import React, {Component} from 'react';
import '../scss/custom.scss'

import axios from 'axios';

//react bootstrap imports
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Alert from 'react-bootstrap/Alert'
// import Jumbotron from 'react-bootstrap/Jumbotron'
import Button from 'react-bootstrap/Button'
import Form from 'react-bootstrap/Form'
// import Card from 'react-bootstrap/Card'
// import CardDeck from 'react-bootstrap/CardDeck'

export default class NewAlert extends Component {

    constructor(props) {
        super(props);

        this.onChangeTicker = this.onChangeTicker.bind(this);
        this.onChangeStance = this.onChangeStance.bind(this);
        this.onChangeEntry = this.onChangeEntry.bind(this);
        this.onChangeExit = this.onChangeExit.bind(this);
        this.onChangeSynopsis = this.onChangeSynopsis.bind(this);
        this.onChangeAuthor = this.onChangeAuthor.bind(this);
        this.onSubmit = this.onSubmit.bind(this);

        this.state = {
            ticker: '',
            stance: '',
            entry: '',
            exit: '',
            synopsis: '',
            author: '',
            show: false
        }
    }

    onChangeTicker(e) {
        this.setState({
            ticker: e.target.value
        });
    }

    onChangeStance(e) {
        this.setState({
            stance: e.target.value
        });
    }

    onChangeEntry(e) {
        this.setState({
            entry: e.target.value
        });
    }

    onChangeExit(e) {
        this.setState({
            exit: e.target.value
        });
    }

    onChangeSynopsis(e) {
        this.setState({
            synopsis: e.target.value
        });
    }

    onChangeAuthor(e) {
        this.setState({
            author: e.target.value
        });
    }

    onSubmit(e) {
        e.preventDefault();
        let stanceBool = true;
        if(this.state.stance === 'true') stanceBool = true;
        else stanceBool = false;
        const newAlert = {
            ticker: this.state.ticker,
            stance: stanceBool,
            entry: this.state.entry,
            exit: this.state.exit,
            synopsis: this.state.synopsis,
            time: new Date(),
            author: this.state.author
        };
        axios.post('https://alertigo-api.herokuapp.com/alerts/add', newAlert)
            .then(res => this.setState({show: true}));

        this.setState({
            ticker: '',
            stance: false,
            entry: '',
            exit: '',
            synopsis: '',
            author: ''
        })
    }

    render() {
        return (
            <Container className='mt-5'>
                <Alert show={this.state.show} className='w-50 bg-secondary border-0 text-center ml-auto mr-auto mb-5 p-4' variant="dark">
                    <Alert.Heading className='text-light mb-4'>Alert Posted Successfully!</Alert.Heading>
                    <Button onClick={() => this.setState({show: false})} variant="primary">
                        Close this popup
                    </Button>
                </Alert>
                <Form as={Row} className='text-center'>
                    <Form.Group as={Col} xs={6} md={2} controlId="ticker">
                        <Form.Control 
                            value={this.state.ticker}
                            onChange={this.onChangeTicker}
                            placeholder="Enter ticker" />
                        <Form.Text muted>
                            For stock options, write your contract here. Example: "AMZN 04/19 1000C"
                        </Form.Text>
                    </Form.Group>
                    <Form.Group as={Col} xs={6} md={2} controlId="entry">
                        <Form.Control 
                            value={this.state.entry}
                            onChange={this.onChangeEntry}
                            placeholder="Entry target" />
                        <Form.Text muted>
                            Doesn't have to be a number. "Below 100" or "Above 400" works
                        </Form.Text>
                    </Form.Group>
                    <Form.Group as={Col} xs={6} md={2} controlId="exit">
                        <Form.Control 
                            value={this.state.exit}
                            onChange={this.onChangeExit}
                            placeholder="Exit target" />
                        <Form.Text muted>
                            Doesnt have to be a number. "Moon", "ATH" etc works
                        </Form.Text>
                    </Form.Group>
                    <Form.Group as={Col} xs={6} md={2} controlId="author">
                        <Form.Control 
                            value={this.state.author}
                            onChange={this.onChangeAuthor}
                            placeholder="Alert author" />
                        <Form.Text muted>
                            help text
                        </Form.Text>
                    </Form.Group>
                    <Form.Group as={Col} xs={6} md={2} controlId="stance">
                        <Form.Check inline 
                            type="radio" 
                            value='true'
                            checked={this.state.stance === 'true'} 
                            onChange={this.onChangeStance}
                            label="BULL"/>
                        <Form.Check inline 
                            type="radio" 
                            value='false'
                            checked={this.state.stance === 'false'} 
                            onChange={this.onChangeStance}
                            label="BEAR"/>
                        <Form.Text muted>
                            Bull = going up, Bear = going down. Just in case :]
                        </Form.Text>
                    </Form.Group>
                    <Form.Group as={Col} xs={12} sm={9} md={10} controlId="synopsis">
                        <Form.Control as='textarea'
                            rows='1'
                            value={this.state.synopsis}
                            onChange={this.onChangeSynopsis}
                            placeholder="Synopsis" />
                    </Form.Group>
                    <Form.Group as={Col}>
                        <Button onClick={this.onSubmit} variant="outline-primary">
                            Submit
                        </Button>
                    </Form.Group>
                </Form>
            </Container>
        )
    }
}